/* Section Padding */
.section-padding {
  padding-top: 5rem;
  padding-bottom: 5rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
@media (min-width: 768px) {

  .section-padding {
    padding-left: 2rem;
    padding-right: 2rem;
  }
